define('parseClient',[],function() {
    function extractStop(obj) {
        return {
            code: obj.get("code"),
            name: obj.get("name"),
            town: obj.get("town"),
            location: obj.get("location")
        };
    }

    return {
        getNearbyStops: function(lat, lng) {
            var point = new Parse.GeoPoint(lat, lng);
            var query = new Parse.Query(Parse.Object.extend("stop"));
            query.near("location", point);
            query.limit(10);
            return query.find().then(function(objs) {
                return $.map(objs, extractStop);
            });
        },

        // getStopCode: function(place) {
        //     lat = Math.round(place.geometry.location.lat() * 1000000) / 1000000;
        //     lng = Math.round(place.geometry.location.lng() * 1000000) / 1000000;
            
        //     var query = new Parse.Query(Parse.Object.extend("stops"));
        //     query.equalTo("lat", lat);
        //     query.equalTo("lon", lng);
        //     query.select("code");
        //     return query.first().then(function(stop) {
        //         place.stopCode = stop.get("code");
        //         return Parse.Promise.as(place);
        //     });
        // },

        getStopByCode: function(stopCode) {
            var query = new Parse.Query(Parse.Object.extend("stop"));
            query.equalTo("code", stopCode);
            return query.first().then(extractStop);
        }
    }
});
